import React from 'react';
import './styles.css';
import { titleCase } from './utils.js';
import { joinString } from './utils';
import { convertUnixTime } from './utils';
import { currentTime } from './utils.js';
import dayjs from 'dayjs';

const language = window.navigator.userLanguage || window.navigator.language;
const langSliced = language.slice(0, 2);
console.log(langSliced);

require(`dayjs/locale/${langSliced}`);


const CardExampleCard = ({weatherData, cityData}) => (
    <div id="main" >
        <div className='header'>
            <div className='header-left'>
                <div className='city'>
                    <h1>{cityData.locality}</h1>
                </div>
                <div className='date'>
                    <span className='text-capitalize'>{dayjs().locale(`${langSliced}`).format('dddd DD MMMM')}</span>
                </div>
                <div className='temperature'>
                    <h2>{Math.floor(weatherData.current.temp)}&deg;</h2>
                </div>
            </div>
            <div className='header-right'>
                <div className='weather-icon'>
                    <img src={(`./icons/animated/${joinString(weatherData.current.weather[0].description) === 'clear-sky' && currentTime() < convertUnixTime(weatherData.current.sunset) ? 'clear-day' : joinString(weatherData.current.weather[0].description) === 'clear-sky' && currentTime() > convertUnixTime(weatherData.current.sunset) ? 'clear-night' : joinString(weatherData.current.weather[0].description)}.svg`)} alt={weatherData.current.weather[0].description} />
                </div>
                <div className='description'>
                    <p>{titleCase(weatherData.current.weather[0].description)}</p> 
                </div>
                <div className='min-max-temperature'>
                    <span>H: {Math.floor(weatherData.daily[0].temp.max)}&deg; </span>
                    <span>L: {Math.floor(weatherData.daily[0].temp.min)}&deg;</span>
                </div>
                <div>
                    <p>Humidity: {weatherData.current.humidity}%</p>    
                </div>
            </div>
        </div> 
       
        <div className='sun-position'>
            <div className='sunrise'>
                <img className='sunrise-sun' src={('./icons/animated/clear-day.svg')} alt="" />
            </div>
            <span>Sunrise: {convertUnixTime(weatherData.current.sunrise)}</span>
            <div className='sunset'>
                <img className='sunset-moon' src={('./icons/animated/clear-night.svg')} alt="" />
            </div>
            <span>Sunset: {convertUnixTime(weatherData.current.sunset)}</span>
        </div>
        
    </div>
)

export default CardExampleCard;


